import React, { Component } from 'react';

//components
import PlHeader from './../../components/pl_component_header/pl_component_header';
import { PlComponentFormItemInputText } from './../../components/pl_component_form/pl_component_form_item/pl_component_form_item_input_text/pl_component_form_item_input_text.jsx';
import { Pagination } from 'carbon-components-react';
import { PlComponentTable } from './../../components/pl_component_table/pl_component_table';

//actions
import {
  dowload_studies_from_api,
  obtainStudiesFromQuery,
  obtain_user_token,
  obtain_user_email_from_token,
  logout,
  devLogin,
  navigate_to_url
} from './pl_page_patients_actions';

import { isObject } from 'util';

//modules
import { isObjectEmpty } from './../../../modules/rkt_module_object';

//config
import config from './../../../config/config.json';

export default class PlPagePatients extends Component {


  constructor() {
    super();

    this.state = {
      studies: [],
      studies_to_display: [],
    }
  }

  setUrl(url) {

    if (url !== false) {
      var browserHistory = this.props.history;
      browserHistory.push("/patients" + url);

    } else {
      alert("URL not compatible");
    }
  }

  download_studies(user_token){

    var myComponent = this;

    var user_email = obtain_user_email_from_token(user_token);

    dowload_studies_from_api(config.url_api+"/studies",user_token, function (result) {

      console.log(result);

      if ("error" in result) {

        myComponent.setState({
          studies: [],
          studies_to_display: [],
          user_authenticated: false,
          user_email:""
        });

      } else {

        myComponent.setState({
          studies: result,
          studies_to_display: result,
          user_authenticated: true,
          user_email:user_email
        });

      }

    });
    
  }

  componentDidMount() {

    //var myComponent = this;

    var location = window.location;

    var user_token = obtain_user_token(location);

    if (user_token) {

      this.download_studies(user_token);

    } else {

      this.setState({
        studies: [],
        studies_to_display: [],
        user_authenticated: false,
        user_email:""
      });

    }
  }

  onChangePagination(element) {
    console.log("On change pagination");
    console.log(element);
  }

  onProcessQuery(index, isInputInvalid, text_field_value) {

    if (!isObjectEmpty(this.state.studies)) {

      var studies = obtainStudiesFromQuery(text_field_value, this.state.studies);

      this.setState({
        studies_to_display: studies
      })

    }
  }

  onLogout(){
    logout();
    navigate_to_url(config.url_app);
  }

  onLogin(){

    var myComponent = this;

    devLogin(config.url_api+"/auth/devtoken","carlos.yague@upf.edu",function(result){
      
      if(result){

        myComponent.download_studies(result);

      }else{

        myComponent.setState({
          user_authenticated:false,
          user_email:""
        });

      }

    });
  }

  onClickRow(study_selected){

    var url_api = config.url_api;
    var patient_id = study_selected.patient;
    var modality_id = study_selected.modality;
    var followup_id = study_selected.date;

    var url_to_navigate = config.url_app + '/viewer?';
    url_to_navigate = url_to_navigate+"url="+url_api+"&";
    url_to_navigate = url_to_navigate+"patient_id="+patient_id+"&";
    url_to_navigate = url_to_navigate+"modality_id="+modality_id+"&";
    url_to_navigate = url_to_navigate+"followup_id="+followup_id+"&";

    navigate_to_url(url_to_navigate);
  }

  render_user_authentication() {

    return (
      <div className="grid-block vertical user-authentication">
        <div className="grid-block align-center shrink">
          Ups! Sorry you must be authenticated
        </div>
        <div className="grid-block align-center shrink">
          <p>click <a href={config.url_api+"/auth/google"}> here </a> {/*<a onClick={this.onLogin.bind(this)}> here </a>*/} for google login</p>
        </div>

      </div>
    );
  }

  render() {

    var data = this.state.studies_to_display;

    var body;

    if ("user_authenticated" in this.state) {

      if (this.state.user_authenticated) {

        if (!isObjectEmpty(data)) {

          body = <PlComponentTable data={data} on_click_row={this.onClickRow.bind(this)}/>;

        } else {

          body = "No studies found in server";

        }

      } else {

        body = this.render_user_authentication();

      }
    }


    return (
      <div className="grid-frame pl-page-patients">
        <div className="grid-block vertical">
          <PlHeader user_email={this.state.user_email} on_logout={this.onLogout.bind(this)}/>
          <div className="grid-block sub-header shrink">
            <div className="grid-block">
              <h3>Study List</h3>
            </div>
            <div className="grid-block shrink">
              <h3>{this.state.studies.length}</h3>
            </div>
          </div>
          <div className="grid-block shrink">
            <PlComponentFormItemInputText
              ref="SearchBar"
              key={"patients_search_bar"}
              required_input={""}
              onInputChange={this.onProcessQuery.bind(this)} />
          </div>
          <div className="grid-block table">
            {body}
          </div>
          <div className="grid-block shrink">
            <Pagination
              onChange={this.onChangePagination.bind(this)}
              pageSizes={[10, 20, 30]}
              pagesUnknown
              pageInputDisabled
            />
          </div>
        </div>
      </div>
    );
  }
}


